import React, { Component } from 'react';
import { withRouter } from 'react-router-dom';
import { Row, Card, Form, Input, message, Spin } from 'antd';
import { SelectAjax } from 'components/common';
import Page from 'components/LayoutComponents/Page';
import { BtnSave, BtnCancel } from 'components/common/button';
import {
  getStudyFields,
  getStudyField,
  updateStudyField,
  createStudyField,
} from 'services/StudyField';
import { getStudyFieldGroups } from 'services/studyFieldGroup';
import { getQueryParamId } from 'utils/urlUtil';

const handleInitialValue = initialValue => {
  const initialValueDefault = {
    id: '',
    label: '',
    labelInEnglish: '',
  };
  return { ...initialValueDefault, ...initialValue };
};

// const { Option } = Select;

@Form.create({ name: 'formSpecializeTraining' })
class FormSpecializeTrainingPage extends Component {
  constructor(props) {
    super(props);
    this.state = {
      systemId: false,
      initialValue: {},
      loading: false,
      // listSelect: [],
    };
  }

  componentDidMount = () => {
    // this.getListData();
    if (getQueryParamId()) {
      this.setState({ systemId: getQueryParamId(), loading: true });
      getStudyField(getQueryParamId())
        .then(res => {
          const initialValue = handleInitialValue(res.data);
          this.setState({ initialValue, loading: false });
        })
        .catch(error => {
          console.log('error', error);
          this.setState({ loading: false });
        });
    }
  };

  handleSubmit = e => {
    e.preventDefault();
    this.props.form.validateFields((err, values) => {
      console.log(values);
      if (!err) {
        if (getQueryParamId()) {
          updateStudyField(getQueryParamId(), values)
            .then(() => {
              message.success('Chỉnh sửa hệ đào tạo thành công');
              this.props.history.goBack();
            })
            .catch(error => {
              console.log('res', error);
              // message.error(error.response.data.message);
            });
        } else {
          createStudyField(values)
            .then(() => {
              message.success('Thêm hệ đào tạo thành công');
              this.props.history.goBack();
            })
            .catch(error => {
              console.log('res', error.response);
              // message.error(error.response.data.message);
            });
        }
      }
    });
  };

  handleSelectStudyField = () => {
    this.props.form.setFieldsValue({ sfId: null });
  };

  render() {
    const { initialValue } = this.state;
    const { getFieldDecorator, getFieldValue } = this.props.form;
    const formItemLayout = {
      labelCol: {
        xs: { span: 24 },
        sm: { span: 5 },
      },
      wrapperCol: {
        xs: { span: 24 },
        sm: { span: 12 },
      },
      colon: false,
    };
    const { systemId, loading } = this.state;
    const sfgId = getFieldValue('sfgId');
    console.log(this.state);
    return (
      <Card title={systemId ? 'Cập nhật chuyên ngành' : 'Thêm chuyên ngành'}>
        <Spin spinning={loading}>
          <Row>
            <Form {...formItemLayout} onSubmit={this.handleSubmit} className="login-form">
              <Form.Item label="Khối ngành">
                {getFieldDecorator('sfgId', {
                  initialValue: initialValue.sfgId,
                  rules: [
                    {
                      required: true,
                      message: 'Vui lòng nhập thông tin!',
                    },
                  ],
                })(
                  <SelectAjax
                    onSelect={() => this.handleSelectStudyField()}
                    service={getStudyFieldGroups}
                  />
                )}
              </Form.Item>
              <Form.Item label="Ngành đào tạo">
                {getFieldDecorator('sfId', {
                  initialValue: initialValue.sfId,
                  rules: [
                    {
                      required: true,
                      message: 'Vui lòng nhập thông tin!',
                    },
                  ],
                })(
                  <SelectAjax
                    service={getStudyFields}
                    params={{ hasSFId: false, sFGId: sfgId }}
                    // allowGetData={!!sfgId}
                  />
                )}
              </Form.Item>
              <Form.Item {...formItemLayout} label="Mã chuyên ngành" hasFeedback>
                {getFieldDecorator('id', {
                  initialValue: initialValue.id,
                  rules: [
                    {
                      required: true,
                      message: 'Vui lòng nhập thông tin!',
                    },
                  ],
                })(<Input disabled={systemId !== false} />)}
              </Form.Item>
              <Form.Item {...formItemLayout} label="Tên chuyên ngành" hasFeedback>
                {getFieldDecorator('label', {
                  initialValue: initialValue.label,
                  rules: [
                    {
                      required: true,
                      message: 'Vui lòng nhập thông tin!',
                    },
                  ],
                })(<Input />)}
              </Form.Item>
              <Form.Item {...formItemLayout} label="Tên tiếng anh" hasFeedback>
                {getFieldDecorator('labelInEnglish', {
                  initialValue: initialValue.labelInEnglish,
                })(<Input />)}
              </Form.Item>
              <Form.Item
                style={{ display: 'none' }}
                {...formItemLayout}
                label="Tên tiếng anh"
                hasFeedback
              >
                {getFieldDecorator('trainTypeId', {
                  initialValue: initialValue.trainTypeId,
                })(<Input />)}
              </Form.Item>
              <Form.Item {...formItemLayout} label=" ">
                <BtnSave htmlType="submit" />
                <BtnCancel onClick={() => this.props.history.goBack()} />
              </Form.Item>
            </Form>
          </Row>
        </Spin>
      </Card>
    );
  }
}

export default withRouter(props => (
  <Page {...props}>
    <FormSpecializeTrainingPage {...props} />
  </Page>
));



